<template>
    <div class="h-screen flex items-center justify-center animated-bg">
      <!-- Welcome Section -->
      <div class="text-center p-10 bg-white bg-opacity-20 backdrop-blur-lg shadow-lg rounded-lg">
        <h1 class="text-4xl md:text-5xl font-bold text-white mb-4">Welcome to NitiQuest</h1>
        <p class="text-lg md:text-xl text-white">Let's start your management learning journey!</p>
        <button @click="showLoginModal" class="mt-8 bg-[#3b5075] hover:bg-[#2e3e5a] text-white font-bold py-3 px-6 rounded-lg shadow-lg">
          Start Now
        </button>
      </div>
  
      <!-- Modal Popup for Login -->
      <div v-if="isLoginModalVisible" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
        <div class="bg-white p-8 rounded-lg shadow-lg max-w-lg w-full">
          <h2 class="text-2xl font-bold text-center mb-4">Login</h2>
          <form @submit.prevent="handleLogin" class="space-y-4">
            <!-- Username -->
            <div>
              <label for="username" class="block text-sm font-medium text-gray-700">Username</label>
              <input
                type="text"
                v-model="username"
                id="username"
                class="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring focus:ring-indigo-200"
                required
              />
            </div>
            <!-- Password -->
            <div>
              <label for="password" class="block text-sm font-medium text-gray-700">Password</label>
              <input
                type="password"
                v-model="password"
                id="password"
                class="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring focus:ring-indigo-200"
                required
              />
            </div>
  
            <!-- Submit Button -->
            <div class="flex justify-center">
              <button type="submit" class="bg-[#3b5075] hover:bg-[#2e3e5a] text-white font-bold py-2 px-4 rounded-lg">
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  
  export default {
    data() {
      return {
        isLoginModalVisible: false,
        username: '',
        password: ''
      };
    },
    methods: {
      showLoginModal() {
        this.isLoginModalVisible = true;
      },
      handleLogin() {
        if (this.username === 'admin' && this.password === 'password') {
          localStorage.setItem('isLoggedIn', 'true');
          this.$router.push('/dashboard'); // Change to the appropriate route after login
        } else {
          alert('Invalid credentials. Please try again.');
        }
      }
    }
  };
  </script>
  
  <style>
  @keyframes backgroundAnimate {
    0% {
      background-position: 0 0;
    }
    100% {
      background-position: 100% 100%;
    }
  }
  
  .animated-bg {
    background: linear-gradient(135deg, #3b5d50, #000296, #3774e6, #3b5d50);
    background-size: 300% 300%;
    animation: backgroundAnimate 10s ease infinite;
    min-height: 100vh; /* Ensures the background covers the entire viewport */
  }
  </style>
  